body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
  background-color: #272B30;
}


h1 {
  padding: 1.5rem 0;
  font-size: 2rem;
  color: #aaa;
  text-align: center;
}

h2 {
  font-size: 26px;
}

.board-row{
  display: grid;
  grid-template-rows: repeat(3, 60px);
  grid-template-columns: repeat(3, 60px);
  justify-content: center;
}

.status {
  margin-bottom: 10px;
}

.next-player {
  color: #fff;
}

.winner-player {
  color: #CC0000;
}

.next-player,
.winner-player {
  margin-left: 15px;
  width: 35px;
  display: inline-block;
  text-align: center;
}

.square {
  background: #fff;
  border: 1px solid rgba(0, 0, 0, 0.6);
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.3);
  font-size: 35px;
  font-weight: bold;
  line-height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: column;
}

.game-info {
  color: #aaa;
  font-size: 20px;
  margin-top: 30px;
}

.info-list {
  width: 100%;
}

.game-board {
  margin: auto;
}

.btn {
  padding: 0.5rem 1.75rem;
  color: #fff;
  background-color: transparent;
  border: 1px solid rgba(0, 0, 0, 0.6);
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.3);
  background-repeat: no-repeat;
}

.btn-move {
  background-image: linear-gradient(#484e55, #3A3F44 60%, #313539);
}

.btn-start {
  display: flex;
  margin: 0 auto 20px;
  background-image: linear-gradient(#8a9196, #7A8288 60%, #70787d);
}

.highlight {
  background-color: #CC0000;
}

.currentMove {
  background-image: linear-gradient(#8a9196, #7A8288 60%, #70787d);
}
